<template>
  <v-card :title="levelMsg.levelID+'. '+levelMsg.levelName" class="levelitem-page">
    <v-timeline>
      <v-timeline-item color="green">难度： {{levelMsg.difficulty}}</v-timeline-item>
      <v-timeline-item color="green">1星过关时间： {{levelMsg.easy_time}}</v-timeline-item>
      <v-timeline-item color="red">
        <v-collapse :bordered="false">
          <v-panel header="地图信息" index="1">
              <p>地图id: {{levelMsg.mapMsg.mapID}}</p>
              <p>地图名称: {{levelMsg.mapMsg.mapName}}</p>
              <p>描述: {{levelMsg.mapMsg.description}}</p>
          </v-panel>
        </v-collapse>
      </v-timeline-item>
      <v-timeline-item>
        起点： ({{levelMsg.startPosX}}, {{levelMsg.startPosY}}, {{levelMsg.startPosZ}})
      </v-timeline-item>
      <v-timeline-item>
        终点： ({{levelMsg.endPosX}}, {{levelMsg.endPosY}}, {{levelMsg.endPosZ}})
      </v-timeline-item>
      <v-timeline-item color="red">
        <p style="margin:8px;">通关奖励</p>
        <v-collapse>
          <v-panel header="皮肤奖励" v-if="levelMsg.skinMsg" index="1">
            <p>皮肤id: {{levelMsg.skinMsg.skinID}}</p>
            <p>皮肤名称: {{levelMsg.skinMsg.skinName}}</p>
            <p>描述: {{levelMsg.skinMsg.description}}</p>
          </v-panel>
          <v-panel header="道具奖励" v-if="levelMsg.propsMsg" index="2">
            <p>道具id: {{levelMsg.propsMsg.propsID}}</p>
            <p>道具名称: {{levelMsg.propsMsg.propsName}}</p>
            <p>描述: {{levelMsg.propsMsg.description}}</p>
          </v-panel>
          <v-panel header="金币奖励" index="3">
            <p>数量: {{levelMsg.levelGold}}</p>
          </v-panel>
        </v-collapse>
      </v-timeline-item>
    </v-timeline>
  </v-card>
</template>

<script>
export default {
  data () {
    return {
      levelMsg: {
        mapMsg: {
          mapID: 0,
          mapName: '',
          description: ''
        }
      }
    }
  },
  watch: {
    '$route' (to, from) {
      let api = "/api/level/"+this.$route.params.id;
      this.$http.get(api).then(res => {
        if(!res.data.code) {
          this.levelMsg = res.data.levelMsg;
        } else {
          this.$message['error']("查询失败");
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    }
  },
  created() {
    let api = "/api/level/"+this.$route.params.id;
    this.$http.get(api).then(res => {
      if(!res.data.code) {
        this.levelMsg = res.data.levelMsg;
      } else {
        this.$message['error']("查询失败");
      }
    }).catch(err => {
      console.log(err);
      this.$message['error']("网络错误");
    });
  }
}
</script>

<style scoped>
.levelitem-page {
  width: 100%;
  height: 100%;
  padding: 24px;
}
</style>



